class MainApi {
    constructor(config) {
      this._url = config.url;
      this.headers = config.headers;
    }

    _checkResponse(res) {
      if (res.ok) {
        return res.json();
      }
      return Promise.reject(`–û—à–∏–±–∫–∞ ${res.status}`);
    }

    //–ø–æ–ª—É—á–µ–Ω–∏–µ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ —Å —Å–µ—Ä–≤–µ—Ä–∞
    getProfileInfo(token) {
      return fetch(`${this._url}/users/me`, {
        method: "GET",
        headers: {
          ...this.headers,
          Authorization: `Bearer ${token}`,
        },
      }).then(this._checkResponse);
    }

    //–ø–µ—Ä–µ–¥–∞—á–∞ –Ω–æ–≤–æ–π –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ –Ω–∞ —Å–µ—Ä–≤–µ—Ä
    putProfileInfo(name, email, token) {
      return fetch(`${this._url}/users/me`, {
        method: "PATCH",
        headers: {
          ...this.headers,
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({
          name: name,
          email: email,
        }),
      }).then(this._checkResponse);
    }

   //–ø–æ–ª—É—á–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–µ–∫ —Å —Å–µ—Ä–≤–µ—Ä–∞
   getAllCard(token) {
      return fetch(`${this._url}/movies`, {
        method: "GET",
        headers: {
        ...this.headers,
        Authorization: `Bearer ${token}`,
       },
       }).then(this._checkResponse);
    }

  //–¥–æ–±–∞–≤–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–∫–∏ –Ω–∞ —Å–µ—Ä–≤–µ—Ä
  addCard(data, token) {
    return fetch(`${this._url}/movies`, {
      method: "POST",
      headers: {
        ...this.headers,
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify({
        country: data.country,
        director: data.director,
        duration: data.duration,
        year: data.year,
        description: data.description,
        image: `https://api.nomoreparties.co${data.image.url}`,
        trailer: data.trailerLink,
        thumbnail: `https://api.nomoreparties.co${data.image.url}`,
        movieId: data.id,
        nameRU: data.nameRU,
        nameEN: data.nameEN
      }),
    }).then(this._checkResponse);
  }

  //—É–¥–∞–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–∫–∏ —Å —Å–µ—Ä–≤–µ—Ä–∞
  removeCard(cardId, token) {
    return fetch(`${this._url}/movies/${cardId}`, {
      method: "DELETE",
      headers: {
        ...this.headers,
        Authorization: `Bearer ${token}`,
      },
    }).then(this._checkResponse);
  }
}
  //–∑–∞–ø–∏—Å—å –≤—Å–µ–≥–æ –∫–ª–∞—Å—Å–∞ Api –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é –∏ –µ—ë –∏–º–ø–æ—Ä—Ç
  const mainApi = new MainApi({
    url: "https://api.mattzenn-movies.nomoredomains.rocks",
    headers: {
      "Content-type": "application/json",
    },
  });

  export default mainApi;