class MoviesApi {
  constructor(config) {
    this._url = config.url;
    this.headers = config.headers;
  }

  _checkResponse(res) {
    if (res.ok) {
      return res.json();
    }
    return Promise.reject(`–û—à–∏–±–∫–∞ ${res.status}`);
  }

  getMovies() {
      return fetch(`${this._url}`, {
        method: "GET",
        headers: {
          ...this.headers,
        },
      }).then(this._checkResponse);
    }
}

