import './App.css';
import '../../vendor/styles/normalize.css';
import React from 'react';
import { CurrentUserContext } from '../../contexts/CurrentUserContext';
import ProtectedRoute from '../ProtectedRoute/ProtectedRoute'
import Main from '../Main/Main'
import { Route, Switch, useHistory, Redirect, useLocation } from "react-router-dom";
import Register from "../Register/Register"
import Profile from '../Profile/Profile';
import Movies from '../Movies/Movies';
import SavedMovies from '../SavedMovies/SavedMovies';
import Login from '../Login/Login'
import Error from '../Error/Error';
import MoviesApi from "../../utils/MoviesApi";
import mainApi from "../../utils/MainApi"
import Preloader from '../Preloader/Preloader';
import * as auth from '../../utils/auth'

export default function App() {

    const history = useHistory();
    const location = useLocation();

    const [isBurger, setIsBurger] = React.useState(false);
    const [isPreloader, setIsPreloader] = React.useState(false);
    const [currentUser, setCurrentUser] = React.useState({});
    const [movies, setMovies] = React.useState([]);
    const [moviesSaved, setMoviesSavied] = React.useState([]);
    const [loggedIn, setLoggedIn] = React.useState(false);
    const [editIsSuccess, setEditIsSuccess] = React.useState(false);
    const [editIsFailed, setEditIsFailed] = React.useState(false);

    function handleBurger() {
        setIsBurger(!isBurger);
    }

    React.useEffect(() => {
        //–≤–∑—è—Ç–∏–µ —Å—Ç–∞—Ç—É—Å–∞ —Ç–æ–∫–µ–Ω–∞ –∏–∑ –ª–æ–∫–∞–ª—å–Ω–æ–≥–æ —Ö—Ä–∞–Ω–∏–ª–∏—â–∞
        const path = location.pathname;
        const token = localStorage.getItem("token");
        if (token) {
            auth.checkToken(token)
                .then((res) => {
                    // setUserEmail(res.email);
                    setLoggedIn(true);

                    //–≤–æ–∑–≤—Ä–∞—â–∞–µ–º –¥–∞–Ω–Ω—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –∏–∑ –±–∞–∑—ã –¥–∞–Ω–Ω—ã—Ö
                    mainApi.getProfileInfo(token).then((result) => {
                        //–≤—ã–ø–æ–ª–Ω—è–µ–º –∑–∞–ø–∏—Å—å –ø–æ–ª—É—á–µ–Ω–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é —Å–æ—Å—Ç–æ—è–Ω–∏—è

                        setCurrentUser(result)
                        localStorage.setItem('currentUser', JSON.stringify(result.data));

                        getStartMovies()
                    })

                    //–¥–∞—ë–º –¥–æ—Å—Ç—É–ø –∫ –æ—Å–Ω–∞–≤–Ω—ã–º —Å—Ç—Ä–∞–Ω–∏—Ü–∞–º
                    history.push(path);
                })
                .catch((err) => {
                    alert(err);
                    localStorage.removeItem("token");
                });
        }
        MoviesApi
            .getMovies()
            .then((data) => {
                setMovies(data)
                localStorage.setItem('films', JSON.stringify(data));
            })
            .catch((err) => {
                alert(err);
            });
    }, []);

    function getStartMovies() {
        setIsPreloader(true);
        const token = localStorage.getItem("token");
        //–ø–æ–∫–∞–∑—ã–≤–∞–µ–º –≤—Å–µ –∫–∞—Ä—Ç–æ—á–∫–∏ –∏–∑ –±–∞–∑—ã –¥–∞–Ω–Ω—ã—Ö
        mainApi
            .getAllCard(token)
            .then((res) => {
                const items = res.map((item) => {
                    return {
                        //–≤–æ–∑–≤—Ä–∞—â–∞–µ–º –º–∞—Å—Å–∏–≤ —Å –Ω—É–∂–Ω—ã–º–∏ –¥–∞–Ω–Ω—ã–º–∏ –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è –Ω–∞—á–∞–ª—å–Ω—ã—Ö –∫–∞—Ä—Ç–æ—á–µ–∫
                        key: item._id,
                        id: item.movieId,
                        image: item.image,
                        nameRU: item.nameRU,
                        duration: item.duration,
                        owner: item.owner,
                        trailer: item.trailer
                    };
                });
                //–≤—ã–ø–æ–ª–Ω—è–µ–º –∑–∞–ø–∏—Å—å —ç—Ç–æ–≥–æ –º–∞—Å—Å–∏–≤–∞ –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é —Å–æ—Å—Ç–æ—è–Ω–∏—è
                setMoviesSavied(items);
            })
            .catch((err) => {
                alert(err);
            })
            .finally(() => setIsPreloader(false))
    }

    function handleCardDelete(_id) {
        const token = localStorage.getItem("token");
        setIsPreloader(true);
        // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –∑–∞–ø—Ä–æ—Å –≤ API –∏ —É–¥–∞–ª—è–µ–º –∫–∞—Ä—Ç–æ—á–∫—É
        mainApi
            .removeCard(_id, token)
            .then((res) => {
                const newMoviesList = moviesSaved.filter((m) => {
                    if (_id === m.id) {
                        return false
                    } else {
                        return true
                    }
                });
                setMoviesSavied(newMoviesList);
            })
            .catch((err) => {
                alert(err);
            })
            .finally(() => setIsPreloader(false))
    }

    //—Ä–µ–≥–µ—Å—Ç—Ä–∏—Ä—É–µ–º –Ω–æ–≤–æ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
    function registerUser(name, email, password) {
        setIsPreloader(true);
        auth.register(name, email, password)
            .then((result) => {
                loginUser(email, password)
                history.push('/signin');
            })
            .catch((err) => {
                alert(err);
            })
            .finally(() => setIsPreloader(false))
    }

    //–≤—Ö–æ–¥ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è 
    function loginUser(email, password) {
        setIsPreloader(true);
        auth.login(email, password)
            .then((res) => {
                if (res.token) {
                    localStorage.setItem("token", res.token);
                    setLoggedIn(true);
                    history.push("/movies");
                    mainApi.getProfileInfo(res.token)
                        .then((result) => {
                            localStorage.setItem('currentUser', JSON.stringify(result.data));
                            //–≤—ã–ø–æ–ª–Ω—è–µ–º –∑–∞–ø–∏—Å—å –ø–æ–ª—É—á–µ–Ω–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é —Å–æ—Å—Ç–æ—è–Ω–∏—è
                            setCurrentUser(result);
                            getStartMovies();
                        });
                }
            })
            .catch((err) => {
                alert(err);
            })
            .finally(() => setIsPreloader(false))
    }

    // –∏–∑–º–µ–Ω–µ–Ω–∏–µ –¥–∞–Ω–Ω—ã—Ö –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
    function handleUpdateUser(name, email) {

        setIsPreloader(true);
        const token = localStorage.getItem("token");

        //–≤—ã–ø–æ–ª–Ω—è–µ–º –∑–∞–ø—Ä–æ—Å –Ω–∞ —Å–µ—Ä–≤–µ—Ä –¥–ª—è –æ—Ç–ø—Ä–∞–≤–∫–∏ –Ω–æ–≤–æ–π –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ
        mainApi
            .putProfileInfo(name, email, token)
            .then((result) => {
                setEditIsSuccess(true);
                //–≤—ã–ø–æ–ª–Ω—è–µ–º –∑–∞–ø–∏—Å—å –ø–æ–ª—É—á–µ–Ω–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é —Å–æ—Å—Ç–æ—è–Ω–∏—è
                setCurrentUser(result);
                setTimeout(() => {
                    setEditIsSuccess(false);
                }, 3000);
            })
            .catch((err) => {
                alert(err);
                setEditIsFailed(true);
                setTimeout(() => {
                    setEditIsFailed(false);
                }, 3000);
            })
            .finally(() => setIsPreloader(false))
    }

    //–≤—ã—Ö–æ–¥ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
    function signOut() {
        localStorage.removeItem("currentUser");
        localStorage.removeItem("token");
        setCurrentUser({});
        setMoviesSavied([]);
        setLoggedIn(false);
        localStorage.clear();
    }

    function handleSaveMovies(card) {
        const token = localStorage.getItem("token");
        setIsPreloader(true);
        // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –∑–∞–ø—Ä–æ—Å –≤ API –∏ —Å–æ–∑–¥–∞—ë–º –∫–∞—Ä—Ç–æ—á–∫—É
        mainApi
            .addCard(card, token)
            .then((item) => {
                const newCard = {
                    //–≤–æ–∑–≤—Ä–∞—â–∞–µ–º –º–∞—Å—Å–∏–≤ —Å –Ω—É–∂–Ω—ã–º–∏ –¥–∞–Ω–Ω—ã–º–∏ –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è –Ω–∞—á–∞–ª—å–Ω—ã—Ö –∫–∞—Ä—Ç–æ—á–µ–∫
                    key: item._id,
                    id: item.movieId,
                    image: item.image,
                    nameRU: item.nameRU,
                    duration: item.duration,
                    owner: item.owner,
                    trailer: item.trailer
                };
                setMoviesSavied([newCard, ...moviesSaved]);
            })
            .catch((err) => {
                alert(err);
            })
            .finally(() => setIsPreloader(false))
    }

    return (
        <div className="app">
            <CurrentUserContext.Provider value={currentUser}>
                <Preloader visible={isPreloader} />
                <Switch>
                    <Route exact path="/">
                        <Main isBurger={isBurger} onBurger={handleBurger} loggedIn={loggedIn} />
                    </Route>

                    <ProtectedRoute path="/movies" component={Movies} isPreloader={isPreloader} setIsPreloader={setIsPreloader} isBurger={isBurger} loggedIn={loggedIn} films={movies} onBurger={handleBurger} saveMovie={handleSaveMovies} moviesSaved={moviesSaved} onDelete={handleCardDelete} />

                    <ProtectedRoute path="/saved-movies" component={SavedMovies} loggedIn={loggedIn} isBurger={isBurger} films={moviesSaved} onBurger={handleBurger} onDeleteCard={handleCardDelete} />

                    <ProtectedRoute path="/profile" component={Profile} loggedIn={loggedIn} isBurger={isBurger} onBurger={handleBurger} signOut={signOut} onUpdateUser={handleUpdateUser} editIsSuccess={editIsSuccess} editIsFailed={editIsFailed} />

                    <Route path="/signup">
                        {loggedIn ? <Redirect to="/" /> : ''}
                        <Register onRegister={registerUser} />
                    </Route>

                    <Route path="/signin">
                        {loggedIn ? <Redirect to="/" /> : ''}
                        <Login onLogin={loginUser} />
                    </Route>

                    <Route path="*">
                        <Error message="–°—Ç—Ä–∞–Ω–∏—Ü–∞ –Ω–µ –Ω–∞–π–¥–µ–Ω–∞" status="404" />
                    </Route>
                </Switch>
            </CurrentUserContext.Provider>
        </div >
    );
}